




















































/**
 * 初始化引导弹框
 * 本组件只会在应用初次加载（未找到本地存储）时调用
 */
import ScreepsApi from '@/plugins/screepsApi'
import Storage from '@/plugins/storage'
import { Component, Prop, Emit, Mixins } from 'vue-property-decorator'

@Component
export default class Boot extends Mixins(ScreepsApi) {
    // 介绍信息
    introduceContent = [
        {
            title: '数据',
            text: '本应用所有数据均保存在本地，自动或手动清除浏览器缓存将可能导致数据丢失（确保浏览器已退出无痕模式），请妥善使用导出 / 导入功能。'
        },
        {
            title: '网络',
            text: '本应用不会优化与 Screeps 的连接质量，在网络不好时可能会出现命令丢失等问题。'
        },
        {
            title: '开源',
            text: '本应用代码已开源至 github。更多信息请访问”关于“页。'
        }
    ]

    // 当前引导所处的页面位置
    step = 1

    // 是否显示该引导页
    @Prop()
    show!: boolean

    // 密码是否可见
    passwordVisiable = false

    // 登陆用户名及密码
    email = ''
    password = ''

    // 令牌，将在登陆成功后获取
    sessionToken = ''

    // 确认按钮是否载入中
    confirmBtnLoading = false

    // 弹出框的基本信息
    showMessage = false
    messageColor = 'success'
    messageText = ''

    // 设置每个阶段的标题
    get currentTitle () {
        switch (this.step) {
        case 1: return '使用须知'
        case 2: return '账户确认'
        case 3: return ''
        default: return '初始化'
        }
    }

    /**
     * 完成初始化工作
     * 在本地新建数据结构并触发完成初始化的回调
     */
    @Emit('on-finish')
    finishBoot(): string {
        Storage.init()

        return this.sessionToken
    }

    /**
     * 回调 - 点击下一步按钮
     */
    next() {
        if (this.step === 2) this.login()
        else if (this.step === 3) this.finishBoot()
        else this.step++
    }

    /**
     * 登陆验证
     * 会确认用户的登陆信息是否正确
     */
    login() {
        if (this.email === '' || this.password === '' || this.confirmBtnLoading) return

        // 设置页面状态
        this.confirmBtnLoading = true
        this.message('info', '正在向 Screeps 服务器验证身份...')

        // 发起请求
        this.getSessionToken(this.email, this.password).then(token => {
            this.sessionToken = token
            this.step++
            this.message('success', '验证成功')
            this.confirmBtnLoading = false
        }).catch(() => {
            this.confirmBtnLoading = false
            this.message('error', '验证失败，请验证用户名密码是否正确')
        })
    }

    // 消息弹窗的封装
    message(color: string, text: string) {
        this.messageColor = color
        this.messageText = text
        this.showMessage = true
    }
}
